.aboutcon{
  max-width: 88rem;
  width: 100%;
  margin: auto;  
  padding-bottom: 20px;
  margin-top: 1rem;
  background-color: aliceblue;
}


.aboutname p{
  text-align: center;
  font-size: 19px;
  padding-top: 1rem;
  padding-bottom: 3rem;
  font-weight: 500;
  color: #4169E1;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.about{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-bottom: 2rem;
}

.oladaxpic {
  position: relative;
  top: -100px;
}

.olami{
  background: linear-gradient(to right, royalblue 50%, purple 50%);
  border-bottom-style: inset;
  border-top-style: inset;
  border-bottom-width: 14px;
  border-top-width: 14px;
  border-top-color: white;
  border-bottom-color: black;
  border-radius: 15px;
}


.olami img {
  width:300px;
  height: 364px;
  background-color: white;
  object-fit: cover;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  object-position: center;;
}

.oladaxpic .olami p{
  font-size: 19px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: white;
}



.aboutsentence h3{
  font-size: 33px;
  font-family: 'fangsong', 'math';
  font-weight: bolder;
  padding: 10px;
  line-height: 1.2;
  color: black;
text-align: justify;
}

  .aboutsentence{
    width: 100%;
    max-width: 550px;
  }

  .aboutsentence p{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 16px;
    padding: 10px;

  }

  .highlight{
    color: purple;
    font-family: 22px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }

  .probutton{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .probutton button{
    background: purple;
    border: none;
    border-radius: 10px;
    color: #fff;
    height: 38px;
    font-size: 16px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center;
    width: 130px;
  }

  .bottom{
    position: static;
    background-color: #3f51b5;
    width: 246px;
    opacity: 0.5;
    border-style: groove;
    height: 150px;
    border-bottom-right-radius: 158px;
    border-top-left-radius: 130px;
    border: none;
    transform: translateY(-500px);
    margin-left: -108px;
    top: -233;
  }


