@media screen and (max-width: 1045px) {
  .hire {
    display: none;
  }

  .first-set {
    display: grid;
    justify-content: center;
    align-items: stretch;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px; /* Add gap between grid items */
    width: 100%;
  }
  .first-set .service1, .service2, .service3 {
    background-color: white;
    text-align: center;
    width: 100%;
    max-width: 350px;
    border-radius: 5px;
  }
}
