@media screen and (max-width: 700px) {
  section {
    display: flex;
    flex-direction: column;
    margin-top: 18px;
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 8px;
    padding-right: 8px;
  }

  .h1 {
    font-size: 23px;
  }

  .service-intro h2 {
    font-size: 23px;
  }

  .first-set .service1 {
    text-align: center;
    width: 100%;
    max-width: 330px;
    border-radius: 5px;
    margin-bottom: 40px;
    transition: transform 0.2s ease-in-out; /* Add a smooth transition effect on the transform property */
  }


  .first-set .service2{
    text-align: center;
    width: 100%;
    max-width: 330px;
    margin-bottom: 40px;
    transition: transform 0.2s ease-in-out; /* Add a smooth transition effect on the transform property */
    border-radius: 5px;
  }

  .first-set .service3 {
    text-align: center;
    width: 100%;
    margin-bottom: 35px;
    transition: transform 0.2s ease-in-out; /* Add a smooth transition effect on the transform property */
    max-width: 330px;
    border-radius: 5px;
  }

  .first-set {
    row-gap: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: column;
  }
}

@media screen and (max-width: 350px) {
